import { Component } from '@angular/core';
import { Title } from "@angular/platform-browser";
import { Router, ActivatedRouteSnapshot, NavigationEnd } from "@angular/router";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  title = "Levitate";
  leftNavLinks = [
    {
      title: "Home",
      href: ""
    },
    {
      title: "News",
      href: "news"
    },
    {
      title: "Team",
      href: "team"
    },
    {
      title: "About",
      href: "about"
    },
    {
      title: "Contact Us",
      href: "contact"
    }
  ];
  rightNavLinks = [
    {
      title: "Discord",
      href: "http://discord.levitate.gg"
    }
  ];

  constructor(private _router: Router, private _title: Title) {}

  private getDeepestTitle(routeSnapshot: ActivatedRouteSnapshot) {
    let title = routeSnapshot.data ? routeSnapshot.data['title'] : '';
    if (routeSnapshot.firstChild) {
      title = this.getDeepestTitle(routeSnapshot.firstChild) || title;
    }
    return title;
  }

  ngOnInit() {
    this._router.events.subscribe((event) => {
      if (event instanceof NavigationEnd) {
        this._title.setTitle(this.getDeepestTitle(this._router.routerState.snapshot.root) + " â€” " + this.title);
      }
    });
  }
}




