import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { ContactUsService } from "./contact.service";
import { GlobalValidators } from "../../utils/custom-validators";

declare let grecaptcha: any;

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss']
})
export class ContactComponent implements OnInit {
  public formComment = '';
  public formClasses = [];

  private form: FormGroup;
  private timeout;

  constructor(private _changeDetectorRef: ChangeDetectorRef,
              private _contactUsService : ContactUsService,
              private _formBulder: FormBuilder) {
    this.form = this._formBulder.group({
      name: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(64)]],
      email: ['', [Validators.required, GlobalValidators.mailFormat]],
      message: ['', [Validators.required, Validators.minLength(8)]]
    });
  }

  ngOnInit() {
  }

  formSubmit() {
    if (this.timeout) {
      clearTimeout(this.timeout);
    }

    let v = grecaptcha.getResponse();

    if (!this.form.valid || !v) {
      this.formComment = 'Your submission was invalid.';
      this.formClasses = ['invalid-submission'];

      this.timeout = setTimeout(
        () => {
          this.formComment = '';
          this.formClasses = [];
        }, 7500);
    } else {
      this._contactUsService.sendEmail(this.form.value).subscribe(result => {
        if (result) {
          this.formComment = 'Successfully submitted your message!';
          this.formClasses = ['valid-submission'];

          this.form.reset();
          let elements = document.querySelectorAll('.mdl-js-textfield');
          for (let i = 0; i < elements.length; i++) {
            (elements[i] as any).MaterialTextfield.checkDirty();
          }

          this.timeout = setTimeout(
            () => {
              this.formComment = '';
              this.formClasses = [];
            }, 7500);
        } else {
          this.formComment = 'Your submission was invalid.';
          this.formClasses = ['invalid-submission'];

          this.timeout = setTimeout(
            () => {
              this.formComment = '';
              this.formClasses = [];
            }, 7500);
        }
      });
    }
  }
}



